.presentation{
    @include container;

    &__content{
        display:flex;
        align-items: center;
        gap:50px;

        &__photo{
            width:250px;
            border-radius: 50%;
        }

        &__txt{
            text-align:center;
            line-height: 1.75;

            &--bold{
                font-weight: 600;
                margin-bottom: 15px;
                font-size: 16px;
                line-height: 1.5;
            }
        }
        
    }
}

.realisations{
    @include container;

    &__content{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 30px;
    }
}